import React, { useState } from "react"
import BingoBoard from "./BingoBoard"
import {generateBoard} from "../generator/board-to-array"
import BingoInfo from "./BingoInfo";


const seed = parseInt(new URLSearchParams(window.location.search).get("seed") || "-1");
if (seed === -1) {
    window.location.href = `?seed=${Math.floor(Math.random() * 999999)}`
}

const explorationSeed = seed + 1765913

const goals = generateBoard(explorationSeed);


function BingoCard() {

    const [goalsCompleted, setGoalsCompleted] = useState(0);
    const onGreen = () =>  setGoalsCompleted(goalsCompleted + 1)
    const onRed   = () =>  {
	setGoalsCompleted(goalsCompleted - 1)
	setOpponentCompleted(opponentCompleted + 1)
    }
    const [opponentCompleted, setOpponentCompleted] = useState(0);
    const onBlack = () => setOpponentCompleted(opponentCompleted - 1)



    const board = seed === -1? <></> : <><BingoBoard goals = {goals} seed = {seed} onGreen = {onGreen} onRed = {onRed} onBlack = {onBlack}/>
                                         <BingoInfo seed = {seed} goalsCompleted = {goalsCompleted} opponentCompleted = {opponentCompleted}/></>

    return (
        <>
        {board}
        </>
    )
}

export default BingoCard
